<template>
  <zk-dialog
    :visible="visible"
    @update:visible="updateVisible"
    :title="title"
    width="440px"
    body-style="padding: 0"
    :customClass="customClass"
    :modal="modal"
    :closeOnClickModal="closeOnClickModal"
    :showClose="showClose"
    :beforeClose="beforeClose"
    @transitionend="handleTransitionend">
    <div :class="['dialog-container', { 'is-center': center }]">
      <div v-if="dangerouslyUseHTMLString" v-html="message"></div>
      <div v-else-if="message">{{ message }}</div>
    </div>
    <div class="dialog-footer">
      <button
        v-if="showCancelButton"
        :class="['dialog-footer-button', cancelButtonClass]"
        type="button"
        @click="onCancelButtonClick">
        <span>{{ cancelButtonText }}</span>
      </button>
      <button
        v-if="showConfirmButton"
        :class="['dialog-footer-button', confirmButtonClass]"
        type="button"
        @click="onConfirmButtonClick">
        <span>{{ confirmButtonText }}</span>
      </button>
    </div>
  </zk-dialog>
</template>

<script>
import Dialog from '../../dialog'

export default {
  name: 'ZkMessageBox',
  components: {
    'zk-dialog': Dialog
  },
  props: {
    //  Dialog .sync 
    visible: {
      type: Boolean,
      default: false
    },
    // Dialog  slot
    title: {
      type: String,
      default: ''
    },
    // MessageBox 
    message: {
      type: String
    },
    // 
    modal: {
      type: Boolean,
      default: true
    },
    //  message  HTML 
    dangerouslyUseHTMLString: {
      type: Boolean,
      default: false
    },
    // Dialog 
    customClass: {
      type: String
    },
    //  modal  Dialog
    closeOnClickModal: {
      type: Boolean,
      default: true
    },
    // 
    showClose: {
      type: Boolean,
      default: true
    },
    //  Dialog 
    beforeClose: {
      type: Function
    },
    // 
    showCancelButton: {
      type: Boolean,
      default: false
    },
    // 
    center: {
      type: Boolean,
      default: true
    },
    // 
    showConfirmButton: {
      type: Boolean,
      default: true
    },
    // 
    cancelButtonText: {
      type: String,
      default: ''
    },
    // 
    confirmButtonText: {
      type: String,
      default: ''
    },
    // 
    cancelButtonClass: {
      type: String,
      default: 'dialog-footer-cancel'
    },
    // 
    confirmButtonClass: {
      type: String,
      default: 'dialog-footer-confirm'
    }
  },
  methods: {
    handleTransitionend (event) {
      this.$emit('transitionend', event)
    },
    updateVisible (visible) {
      this.$emit('update:visible', visible)
      if (!visible) this.$emit('close')
    },
    onConfirmButtonClick () {
      this.$emit('update:visible', false)
      this.$emit('confirm')
    },
    onCancelButtonClick () {
      this.$emit('update:visible', false)
      this.$emit('cancel')
    }
  }
}
</script>

<style lang="scss" scoped>
.dialog-container {
  padding: 64px 30px;
  box-sizing: border-box;
  font-size: 18px;
  color: #656565;
  &.is-center {
    text-align: center;
  }
}
.dialog-footer {
  text-align: center;
  padding: 0 30px 24px;
  .dialog-footer-button {
    width: 180px;
    height: 44px;
    box-sizing: border-box;
    display: inline-block;
    text-align: center;
    color: #656565;
    background: #fff;
    border: 1px solid #AAAAAA;
    margin-right: 9px;
    font-size: 18px;
    cursor: pointer;
    &:last-child {
      margin-right: 0;
    }
    &.dialog-footer-confirm {
      color: #FFFFFF;
      background-color: #0B7AFB;
      border: none;
    }
  }
}
</style>
