//  currentChecked currentDisabled
// 
// 

import { FormItemSlot } from '../form'

export default {
  componentName: 'ZkRadio',
  mixins: [FormItemSlot],
  props: {
    label: {
      default: true
    }
  },
  computed: {
    currentChecked () {
      return this.currentValue === this.label
    }
  },
  watch: {
    currentValue () {
      this.$emit('change', this.currentValue)
    }
  }
}
